body {
  margin: 0;
  font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
  background-color: #0e0e0e;
  color: #ffffff;
}

.app-container {
  display: flex;
  flex-direction: column;
  align-items: center;
  min-height: 100vh;
  padding: 30px;
  background: #0e0e0e;
}

h1 {
  font-size: 2.5rem;
  color: #28d1f7;
  margin-bottom: 20px;
  font-weight: 600;
  text-align: center;
}

.tabs {
  display: flex;
  justify-content: center;
  margin-bottom: 20px;
}

.tab-button {
  background-color: #1e1e1e;
  color: #ccc;
  border: 1px solid #444;
  padding: 12px 20px;
  cursor: pointer;
  font-weight: 500;
  margin: 0 8px;
  border-radius: 5px;
  transition: all 0.3s ease;
}

.tab-button:hover {
  background-color: #2c2c2c;
  color: #fff;
}

.tab-button.active {
  background-color: #28d1f7;
  color: #000;
  font-weight: bold;
  border-color: #28d1f7;
}

.levels-container {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(200px, 1fr));
  gap: 30px;
  max-width: 1200px;
  width: 100%;
}

.level-box {
  background-color: #1a1a1a;
  border-radius: 12px;
  padding: 18px;
  border: 1px solid #333;
  box-shadow: 0 0 12px rgba(40, 209, 247, 0.1);
}

.level-box h3 {
  margin-top: 0;
  color: #ffffff;
  font-size: 1.1rem;
  margin-bottom: 10px;
  border-bottom: 1px solid #333;
  padding-bottom: 6px;
}

.level-box input {
  width: 100%;
  margin-bottom: 8px;
  padding: 8px;
  border: none;
  border-radius: 6px;
  background-color: #2b2b2b;
  color: #f0f0f0;
  font-size: 1rem;
}

.level-box input:focus {
  outline: 2px solid #28d1f7;
}

button {
  padding: 12px 24px;
  font-size: 1rem;
  background-color: #28d1f7;
  color: #000;
  border: none;
  border-radius: 8px;
  cursor: pointer;
  margin-top: 20px;
  transition: background-color 0.3s ease;
}

button:hover {
  background-color: #20bde3;
}

.settings-panel, .pattern-review {
  max-width: 800px;
  margin: 0 auto;
  padding: 20px;
  background-color: #181818;
  border-radius: 10px;
  border: 1px solid #2c2c2c;
  box-shadow: 0 0 12px rgba(255, 255, 255, 0.05);
}

.settings-panel h2, .pattern-review h2 {
  color: #28d1f7;
  font-size: 1.8rem;
  margin-bottom: 20px;
}

input[type="text"], input[type="email"] {
  padding: 10px;
  width: 100%;
  margin-bottom: 15px;
  background-color: #2a2a2a;
  border: 1px solid #444;
  color: #fff;
  border-radius: 6px;
  font-size: 1rem;
}

input[type="text"]:focus, input[type="email"]:focus {
  outline: none;
  border-color: #28d1f7;
  box-shadow: 0 0 0 2px rgba(40, 209, 247, 0.3);
}
